.content__desc {

  @include tablet {

    display: block;
    text-align: center;
  }

  display: flex;
  margin-top: 100px;


}

.content__title-box {

  @include mobile{

    font-size: 24px;
  }

  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 54px;
  text-align: center;
  text-transform: capitalize;
  color: #000000;
  margin-top: 50px;


}

.content__title {


  @include mobile{

    margin-left: 0;
  }

  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
  margin-left: 40px;

}

.content__text {

  @include tablet{

    max-width: 100%;
    margin-left: 0;
  }

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: justify;
  letter-spacing: 0.02em;
  color: rgba(21, 21, 21, 0.6);
  margin-left: 40px;


}

.content__desc-holder {
  display: flex;
  margin-top: 100px;

  @include tablet {

    display: block;
    text-align: center;
  }
}

.content__box {

  @include tablet{

    margin-right: 0;
  }

  margin-right: 40px;


}

.content__img-left,
.content__img-right,
.img-adaptive {

  @include tablet {

    max-width: 450px;
    border-radius: 10px;
  }

  @include small-tablet{

    max-width: 400px;
  }

  @include mobile{

   max-width: 300px;
  }
}

.content__img-right {

  @include tablet {

    display: none;
  }
}

.img-adaptive {

  @include tablet {

    display: block;
    margin: 0 auto;
  }

  display: none;


}
