.form {
  position: relative;
  bottom: 97px;
}

.small {
  font-size: .7em;
}

.square {
  width: 20px;
  height: .7em;
  margin: .5em;
  display: inline-block;
}

.custom-dropdown {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 10px;
}

.custom-dropdown select {
  color: rgb(0, 0, 0);
  font-size: inherit;
  padding: 0em;
  padding-right: 0em;
  cursor: pointer;
  width: 85px;
  border: 0;
  margin: 0;
  border-radius: 3px;
  text-indent: 0.01px;
  text-overflow: '';
  -webkit-appearance: button;
}

.dropdown-box {

  @include mobile{

    text-align: center;
    width: 151px;
    margin: 0 auto;
  }

  margin-left: 55px;


}

.custom-dropdown::before,
.custom-dropdown::after {
  content: "";
  position: absolute;
  pointer-events: none;
}

.custom-dropdown::after {
  content: '';
  height: 1em;
  cursor: pointer;
  font-size: .625em;
  line-height: 1;
  right: 1.2em;
  top: 50%;
  margin-top: -.5em;
}

.custom-dropdown::before {
  width: 1em;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 0 3px 3px 0;
}

.custom-dropdown select[disabled] {
  color: rgba(0, 0, 0, .3);
}

.custom-dropdown select[disabled]::after {
  color: rgba(0, 0, 0, 0.1);
}

.custom-dropdown::after {
  color: transparent;
}

//airports

.dropdown-airports,
.dropdown__content,
.dropdown-calendar {

  @include mobile{

    display: inline-table;
    width: 100%;
    text-align: center;
  }

  display: flex;
  justify-content: center;


}

.form__content {


  @include mobile{

    margin-top: 70px;
  }

  background: #FFFFFF;
  box-shadow: 5px 5px 20px #ccc, -5px -5px 20px #ccc;
  border-radius: 12px;
  padding: 5px 0px 45px 0px;
  position: relative;

}

.dropdown-title {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  margin-top: 10px;
  color: rgba(21, 21, 21, 0.6);
}

.airports-name {
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba(21, 21, 21, 0.6);
}

.dropdown-calendar {

  @include tablet{

    padding: 0 0;
    max-width: 235px;
  }

  @include small-tablet{
    width: 207px;
    height: 104px;
  }

  width: 380px;
  height: 117px;
  border: 1px solid #AAAAAA;
  padding: 0 10px;
  border-radius: 10px;
  justify-content: space-between;
}

.dropdown__airports-box {

  @include tablet{

    max-width: 216px;
  }

  @include small-tablet{
    width: 113px;
    height: 104px;
  }

  @include mobile{

    width: 100%;
    margin: 0 auto 20px;
  }

  margin-right: 30px;
  width: 250px;
  height: 117px;
  border: 1px solid #AAAAAA;
  padding: 0 10px;
  border-radius: 10px;


}

.tumbler-svg {
  position: relative;
  top: 44px;
  right: 18px;
  cursor: pointer;
}

.input__airport {

  @include tablet{
    max-width: 130px;
  }

  @include small-tablet{

    max-width: 70px;
  }

  border: none;
  width: 200px;
  height: 50px;
  margin: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  text-transform: capitalize;
  color: #151515;

  &::placeholder {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    text-transform: capitalize;
    color: #151515;
  }


}

.input__calendar {

  @include tablet{

    width: 102px;
    &::placeholder{
      font-size: 18px;
    }

  }

  border: none;
  width: 130px;
  height: 50px;
  margin: 0;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
  text-transform: capitalize;
  color: #151515;

  &::placeholder {
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    text-transform: capitalize;
    color: #151515;
  }

}
.svg__form{

  @include tablet{

    display: none;
  }
}

.add {


  @include mobile{

    left: 14px;
    bottom: 0;
    display: flex;
    align-items: end;
    justify-content: center;
  }

  position: relative;
  top: 11px;
  right: 50%;
  left: 35%;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-decoration-line: underline;
  text-transform: capitalize;
  color: #151515;

}

.dropdown__button-box {
  position: relative;
}

.dropdown-button {

  @include small-tablet{

    padding: 1px 44px 26px 20px;
  }

  position: absolute;
  left: 0;
  margin: 0 auto;
  right: 0;
  display: inline-table;
  margin-top: 12px;
  background: var(--accent-color);
  color: #fff;
  border-radius: 10px;
  padding: 6px 74px 31px 26px;
  &:hover{
    box-shadow: 1px 5px 10px var(--accent-color), -1px -5px 10px var(--accent-color);
  }


}

.plane-svg {
  width: 40px;
  height: 40px;
  position: relative;
  top: 18px;
  left: 23px;
}
