.footer {
  background: var(--accent-color);
  margin-top: 50px;
  height: 170px;

  &__logo {
    margin: 0 auto;
    display: block;
    padding-top: 50px;
  }

  &__content-inner {

    @include mobile{

      display: block;
      margin-top: 30px;
    }

    display: flex;
    justify-content: space-between;
    margin-top: 60px;
    border-top: 1px solid #ccc;
    color: #fff;


  }
}

.nav__list-footer {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.nav__item-footer+.nav__item-footer {
  margin-left: 20px;
}

.nav__link-footer {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: capitalize;
  color: rgba(255, 255, 255, 0.7);
  transition: all .2s ease;
  &:hover{
    color: #fff;
  }
}

.all__rights-footer {

  @include mobile{

    position: relative;
    top: 10px;
  }

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  margin: 0;
  padding: 0;
  line-height: 21px;
  text-align: center;
  text-transform: capitalize;
  color: rgba(255, 255, 255, 0.7);


}
