
.private-jet{
  margin-top: 100px;
}
.content__title-box {
  margin-top: 100px;
}

.privatejet__content {

  @include tablet{

    display: block;
  }

  display: flex;
  justify-content: space-around;


}

.content__sidebar {
  background: #FFFFFF;
  box-shadow: 0px 30px 84px rgba(19, 10, 46, 0.08), 0px 8px 32px rgba(19, 10, 46, 0.07), 0px 3px 14px rgba(19, 10, 46, 0.03), 0px 1px 3px rgba(19, 10, 46, 0.13);
  border-radius: 12px;
  padding: 20px 10px;
  margin-top: 20px;
}

.sidebar-icon {
  text-align: center;
  margin: 0 auto;
  display: block;
}

.sidebar-title {
  text-align: center;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #151515;
  margin: 0;
}

.sidebar-text {

  @include tablet{

    margin: 0 auto;
  }

  width: 270px;
  text-align: center;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  letter-spacing: 0.02em;
  color: rgba(21, 21, 21, 0.6);


}

.plane {

  @include tablet{

    display: none;
  }

  width: 450px;


}
