.price {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  color: #161616;
}

.price__content {

  @include tablet{

    display: block;
  }

  display: flex;
  justify-content: space-around;
  margin-top: 100px;


}

.price__box {

  @include tablet{

    text-align: center;
    margin-top: 50px;
  }

  background: #F5F5F5;
  box-shadow: 5px 5px 10px #ccc, -5px -5px 10px #ccc;
  border-radius: 20px 20px 20px 20px;
  transition: all 0.2s ease;
  padding: 20px;

  &:hover {
    background: #656293;
    border-radius: 20px;
    // margin-top: 30px;
    bottom: 30px;
    position: relative;
    color: #fff;

    .price__title,
    .price__list-item,
    .price {
      color: #fff;
    }

    .price__list-item {
      &::before {
        content: url(../../img/svg/check-circle-white.svg);
        position: relative;
        top: 5px;
        right: 5px;
      }
    }

    .price__btn {
      background-color: #fff;
      color: #000;
    }

  }



}

.price__title {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  color: #151515;
}

.price__text {
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 33px;
  color: #A9A9AA;
}

.price__list {
  padding: 0;
  border-bottom: 1px dotted #000;
  padding-bottom: 20px;
}

.price__list-item {
  list-style: none;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #151515;

  &::before {
    content: url(../../img/svg/check-circle-black.svg);
    position: relative;
    top: 5px;
    right: 5px;
  }
}

.price__btn {
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  display: block;
  line-height: 40px;
  text-align: center;
  color: #FFFFFF;
  background: #656293;
  border-radius: 12px;
  padding: 12px 25px;
}
.footer__plane{
  margin: 0 auto;
  display: block;
  position: relative;
  top: 20px;
}
