.nav {

  z-index: 10000;
  @include tablet{

    position: fixed;
    width: 100%;
    text-align: center;
  }

  &__list {
    @include flex-v-center;
  }

  &__item{
    &:not(:last-child){
      margin-right: 20px;
    }
    @include tablet{

      margin-top: 40px;
    }
  }
  &__link {
    font-style: 400;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    text-transform: capitalize;
    color: #181818;
    margin-top: 10px;
  }
  &__list{
    @include tablet{

      display: block;
      background: rgba(122, 145, 135, 0.5);
      backdrop-filter: blur(10px);
      border-radius: 10px;
      width: 96%;
      height: 60vh;
      z-index: 5000;
      position: absolute;
      transition: all .8s ease;
      margin-top: -580px;
    }

    @include mobile{

      width: 88%;
    }
  }
  .menu--active{
    margin-top: 112px;
  }

}
