.burger {

  @include tablet {

    display: block;
    position: fixed;
    right: 0;
    z-index: 100000;
  }

  --burger-width: 44px;
  --burger-height: 22px;
  --burger-line-height: 3px;

  position: relative;
  border: none;
  padding: 0;
  width: 44px;
  height: 22px;
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: none;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 90%;
    height: 3px;
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 70%;
    border-radius: 10px;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    &::before {
      top: 40%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 40%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }


}

