.login-page {

  @include mobile{

    width: 240px;
  }

  width: 360px;
  padding: 8% 0 0;
  margin: auto;


}
.form__modals {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form__modals input {
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  border-radius: 10px;
}
.form__modals button {
  text-transform: uppercase;
  outline: 0;
  background: #656293;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form__modals button:hover,.form__modals button:active,.form__modals button:focus {
  background: #656293;
}
.form__modals .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form__modals .message a {
  color: #656293;
  text-decoration: none;
}
.form__modals .register-form {
  display: none;
}
.graph-modal__close{
  right: 40px;
}
