

.plane__items {
  background: #FFFFFF;
  box-shadow: 5px 5px 10px #ccc, -5px -5px 10px #ccc;
  border-radius: 12px;
  padding: 10px;
  margin: 10px;
}

.charters__text-slider {
  text-align: center;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
.plane__nav{
  position: relative;
}
.plane__btn-slider{
  position: absolute;
  right: 0;
}

.plane__slider-btn {

  @include bg-cover;

  width: 40px;
  height: 40px;
  position: relative;

  &--prev {
    background-image: url(../img/svg/prev.svg);
    transition: all 0.2s ease;
    left: 0;
    position: relative;

    &:hover {
      background-image: url(../img/svg/hover-plane-next.svg);
    }

  }

  &--next {
    background-image: url(../img/svg/next.svg);
    transition: all 0.2s ease;
    position: relative;
    right: 0;

    &:hover {
      background-image: url(../img/svg/hover-plane-prev.svg);
    }

  }
}

.image {

  @include tablet{


    margin: 0 auto;
    display: block;
  }

  @include small-tablet{

    margin: 0 auto;
    display: block;
  }

  width: 253px;
  height: 220px;
  border-radius: 10px;


}
.plane-slider{
  margin-top: 100px;
}
.swiper-slide{
  position: relative;
}
