.header{

  @include mobile{

    background-position: right;
    height: 70vh;
  }

  display: block;
  left: 0;
  top: 0;
  width: 100%;
  background-image: url(../img/header-bg.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  &__container{
    @include flex-all-sb;
  }
  &__btn{

   @include mobile{

    position: relative;
    right: 48%;
   }

    margin-right: 30px;
  }
  &__btn-big{

    @include mobile{

      right: 0;
    }
  }
  &__btn-link{

    @include tablet{

      margin: 0 auto;
    }

    @include mobile{

      position: absolute;
      top: 63px;
    }
  }
  .header__btn-register{

    @include mobile{

      position: relative;
      left: 0;
    }
  }
  .logo{
    margin-top: 10px;
  }


}



