.header__desc {

  &-box {
    position: absolute;
    display: block;
    top: 230px;
  }

  &-title {

    @include mobile {

      font-size: 37px;
      line-height: 54px;
      position: relative;
      bottom: 75px;
      // text-shadow: 3px 15px 4px black;
      text-align: center;
    }

    font-style: normal;
    font-weight: 400;
    font-size: 57px;
    line-height: 77px;
    text-transform: capitalize;
    color: #151515;

    span {

      @include mobile {
        color: #4b4e6d;
        text-shadow: 10px 10px 10px #585d92;
      }

      color: var(--accent-color);
      font-weight: 600;
      text-transform: uppercase;


    }


  }

  &-btn {

    @include mobile {

      position: relative;
      bottom: 55px;
      margin: 0 auto;
      display: block;
      text-align: center;
    }

    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #151515;

    svg {
      position: relative;
      top: 3px;
      left: 5px;
    }


  }
}
